import { _decorator, Component, Node, Collider2D, Contact2DType } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PhysicsObject')
export class PhysicsObject extends Component {
    start() {

        let collider = this.getComponent(Collider2D);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onCollisionEnter, this);
            collider.on(Contact2DType.END_CONTACT, this.onCollisionOut, this);
        }

    }
    
    protected onCollisionEnter(self: Collider2D, other: Collider2D) :void {
    
    }

    protected onCollisionOut(self: Collider2D, other: Collider2D) :void {
    
    }

    private _destoryCallback:Function=(sender:PhysicsObject)=>{};
    //define a external callback function
    /***
     * when destroy, call this function(sender:PhysicsObject)
     */
    public get destoryCallback():Function{
        return this._destoryCallback;
    }
    public set destoryCallback(value:Function){
        this._destoryCallback=value;
    }
}

