import { _decorator, Component, Enum, Node, director, Canvas, UITransform, Vec3, v3 } from 'cc';
import { PhysicsObject } from './PhysicsObject';
import { MoveDirection } from './Enums';
const { ccclass, property } = _decorator;

@ccclass('YMoveable')
export class YMoveable extends PhysicsObject {

    //@property(Number)
    public speed: number = 100;
    //@property(Number)
    @property({ type: Enum(MoveDirection) })
    public direction: MoveDirection = MoveDirection.MoveUp;

    public targetXY: Vec3= v3(0,0,0);
    private fromPos: Vec3= v3(0,0,0);

    start() {
        super.start();
        this.fromPos = director.getScene().getComponentInChildren(Canvas).getComponent(UITransform).convertToWorldSpaceAR(this.node.position);
    }

    update(deltaTime: number) {
        /****
         * direction, if=1 then move bullet up, else then move down, if bullet is out of screen, then destroy it
         */
        //if direction=1, then move bullet up

        let screenWidth = director.getScene().getComponentInChildren(Canvas).getComponent(UITransform).contentSize.width;
        let screenHeight = director.getScene().getComponentInChildren(Canvas).getComponent(UITransform).contentSize.height;

        if (this.direction == MoveDirection.MoveUp) {
            this.node.setPosition(this.node.position.x, this.node.position.y + this.speed * deltaTime);
        } else if (this.direction == MoveDirection.MoveDown) {
            this.node.setPosition(this.node.position.x, this.node.position.y - this.speed * deltaTime);
        } else if (this.direction == MoveDirection.MoveXY) {
            let deltaX = Math.abs(this.targetXY.x - this.fromPos.x);
            let deltaY = Math.abs(this.targetXY.y - this.fromPos.y);
            var distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);

            var x = this.node.position.x;
            var y = this.node.position.y;
            if (this.targetXY.y > this.fromPos.y) {
                y += this.speed * (deltaY/distance) * deltaTime
            }
            else {
                y -= this.speed * (deltaY/distance) * deltaTime
            }
            if (this.targetXY.x > this.fromPos.x) {
                x += this.speed * (deltaX/distance) * deltaTime
            }
            else {
                x -= this.speed * (deltaX/distance) * deltaTime
            }
            this.node.setPosition(x, y);
        }

        if (this.node.position.y <= -screenHeight / 2
            || this.node.position.y >= screenHeight / 2
            || this.node.position.x <= -screenWidth / 2
            || this.node.position.x >= screenWidth / 2
        ) {
            this.node.destroy();
        }
    }
}


