import { _decorator, AudioSource, ProgressBar, sys, SpriteFrame, AudioClip, Node, EventTouch, instantiate, director, AudioSourceComponent, UITransform, v3, Prefab, Canvas, Collider2D, Contact2DType, Sprite } from 'cc';
const { ccclass, property } = _decorator;
import { Bullet } from "./bullet";
import { PhysicsObject } from "./common/PhysicsObject";
import { Explosion } from "./common/explosion";
import { Enemy } from './enemy';
import { Weapon } from './weapon';
import { BulletTargetType, MoveDirection, GameStatus } from './common/Enums';
import { GameContext } from './gameContext';

@ccclass('Player')
export class Player extends PhysicsObject {

    private lastShootTime: number = 0;

    private bulletSetting: {
        playerSpriteFrame: SpriteFrame,
        bulletSpeed: number,
        bulletInterval: number,
        bulletDamage: number,
        numberOfBullets: number,
        bulletSpriteFrame: SpriteFrame,
        duration: number;
    };

    @property(Prefab)
    private defaultWeapon: Prefab = null;
    @property(Prefab)
    private bulletPrefab: Prefab = null;
    @property(AudioClip)
    private fireSound: AudioClip = null;
    @property(AudioClip)
    private pickupSound: AudioClip = null;
    @property(Prefab)
    private explosionPrefab: Prefab = null;
    private explosionNode: Node = null;

    @property(ProgressBar)
    private progressBar: ProgressBar = null;

    start() {
        super.start();
        this.lastShootTime = this.bulletSetting.bulletInterval;
    }

    private useWeapon(weapon: Weapon) {
        //assign bullet setting
        this.bulletSetting = {
            playerSpriteFrame: weapon.playerSpriteFrame,
            bulletSpeed: weapon.bulletSpeed,
            bulletInterval: weapon.bulletInterval,
            bulletDamage: weapon.bulletDamage,
            numberOfBullets: weapon.numberOfBullets,
            bulletSpriteFrame: weapon.bulletSpriteFrame,
            duration: weapon.duration
        };
        //change player skin
        if (weapon.playerSpriteFrame != null) {
            this.node.getComponent(Sprite).spriteFrame = this.bulletSetting.playerSpriteFrame;
        }
        if (weapon.duration > 0) {
            this.progressBar.progress = 1;
            //set the position of progress bar to the bottom of player
            this.progressBar.node.active = true;
        } else {
            this.progressBar.node.active = false;
        }
    }

    onCollisionEnter(self: Collider2D, other: Collider2D) {

        if (this.node.active) {

            //if other is enemy, then game over
            if (other.node.getComponent(Enemy) != null || other.node.getComponent(Bullet)?.targetType == BulletTargetType.ToPlayer) {
                this.node.active = false;
                GameContext.instance.gameStatus = GameStatus.Over;
                if (this.explosionPrefab) {
                    let explosionNode = instantiate(this.explosionPrefab);
                    explosionNode.setPosition(this.node.position);
                    explosionNode.parent = this.node.parent;
                    explosionNode.getComponent(UITransform).setContentSize(this.node.getComponent(UITransform).contentSize);
                    explosionNode.getComponent(Explosion).removeDelay = 0.2;
                    explosionNode.active = true;
                }
                this.node.removeFromParent();
                this.scheduleOnce(() => {
                    if (this.destoryCallback != null)
                        this.destoryCallback(this);
                    this.node.destroy();
                }, 0.5);
            }
            //else if award, then upgrade player
            else if (other.node.getComponent(Weapon) != null) {
                //upgrade player
                let audioSource = new Node().addComponent(AudioSource);
                audioSource.clip = this.pickupSound;
                audioSource.play();
                this.useWeapon(other.node.getComponent(Weapon));
            }
        }
    }

    onLoad() {
        if (this.defaultWeapon != null) {
            this.useWeapon(instantiate(this.defaultWeapon).getComponent(Weapon));
        }
    }

    update(deltaTime: number) {
        //if game is pause, then return
        if (GameContext.instance.gameStatus == GameStatus.Playing) {
            this.lastShootTime += deltaTime;
            if (this.lastShootTime >= this.bulletSetting.bulletInterval) {
                this.shoot();
                this.lastShootTime = 0;
            }
            //update progress bar
            if (this.progressBar.node.active) {
                this.progressBar.progress -= deltaTime / this.bulletSetting.duration;
                if (this.progressBar.progress <= 0) {
                    this.progressBar.node.active = false;
                    this.useWeapon(instantiate(this.defaultWeapon).getComponent(Weapon));
                }
            }
        }
    }

    //shoot bullet
    shoot() {
        //generate multiple columns of bullets according to numberOfBullets
        let playerSize = this.node.getComponent(UITransform).contentSize;
        let bulletRangeWidth = playerSize.width;
        if (this.bulletSetting.numberOfBullets > 4) {
            bulletRangeWidth = playerSize.width * Math.min(this.bulletSetting.numberOfBullets / 4, 1.5);
        }

        for (let i = 1; i <= this.bulletSetting.numberOfBullets; i++) {
            //get bullet prefab
            //create bullet
            let bullet = instantiate(this.bulletPrefab);
            //convert bullet to Buttle
            let bulletComponent = bullet.getComponent(Bullet);
            //set bullet direction
            bulletComponent.direction = MoveDirection.MoveUp;
            //set bullet speed
            bulletComponent.speed = this.bulletSetting.bulletSpeed;
            bulletComponent.sender = this;
            bulletComponent.damage = this.bulletSetting.bulletDamage;
            if (this.bulletSetting.bulletSpriteFrame != null) {
                //set bullet spriteFrame
                bullet.getComponent(Sprite).spriteFrame = this.bulletSetting.bulletSpriteFrame;
            }
            let bulletX = this.node.position.x + bulletRangeWidth * i / (this.bulletSetting.numberOfBullets + 1) - bulletRangeWidth / 2;
            //set bullet position 
            bullet.setPosition(bulletX, this.node.position.y + this.node.getComponent(UITransform).contentSize.height / 2);
            //set bullet parent
            bullet.parent = director.getScene().getComponentInChildren(Canvas).node;
            //set bullet active
            bullet.active = true;
        }
        //play fire sound
        let audioSource = new Node().addComponent(AudioSource);
        audioSource.clip = this.fireSound;
        audioSource.play();
    }
}

