import { _decorator, AudioClip, SpriteFrame, Component, Node, CCString, CCInteger, CCFloat } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GameLevel')
export class GameLevel extends Component {

    @property(CCInteger)
    public level:number=1;
    @property(CCFloat)
    public enemyInterval: number = 1;
    @property(CCFloat)
    public enemyShootThreshold: number = 1;
    @property(CCInteger)
    public enemyShootSpeed: number = 100;
    @property(CCInteger)
    public enemySpeed: number = 300;
    @property(CCFloat)
    public enemyShootInterval: number = 1;
    @property(CCFloat)
    public awardInterval: number = 5;
    @property(CCInteger)
    public levelUpScore: number = 100;
    @property(CCString)
    public levelName:string="";


    @property(SpriteFrame)
    public gameBgSpriteFrame: SpriteFrame = null;

    @property(AudioClip)
    public gameBgm:AudioClip=null;

}

