import { _decorator, Component, Enum, tween, CCBoolean, Node, director, v3, Canvas, UITransform, Collider2D, Contact2DType, SpriteFrame, Sprite } from 'cc';

import { BulletTargetType, GameStatus } from './common/Enums';
import { YMoveable } from './common/YMoveable';
import { GameContext } from './gameContext';

const { ccclass, property } = _decorator;

@ccclass('Bullet')
export class Bullet extends YMoveable {

    public sender: Component = null;
    public damage: number = 0;
    public speed: number = 100;
    @property(CCBoolean)
    public hasAnimation: boolean = false;
    @property({ type: Enum(BulletTargetType) })
    public targetType: BulletTargetType = BulletTargetType.ToEnemy;

    public useSkin(frame: SpriteFrame): void {
        //change Sprite Frame
        this.node.getComponent(Sprite).spriteFrame = frame;
    }

    start() {
        super.start();
        if(this.hasAnimation)
        {
            tween(this.node).repeatForever(
                tween()
                .by(0.3, {scale: v3(1, 1, 1)})
                .by(0.3, {scale: v3(-1, -1, -1)})
                ).start();
        }
    }

    update(deltaTime: number): void {
        if (GameContext.instance.gameStatus != GameStatus.Pause) {
            super.update(deltaTime);
        }
    }
    //碰撞检测
    onCollisionEnter(self: Collider2D, other: Collider2D) {
        this.node.destroy();
    }
}

