
import { _decorator, CCInteger, Component, Node, SpriteFrame, Collider2D, AudioSourceComponent } from 'cc';
import { YMoveable } from './common/YMoveable';
import { GameContext } from './gameContext';
import { GameStatus } from "./common/Enums";
import { CCFloat } from 'cc';
const { ccclass, property } = _decorator;

/*****
 * Weapon class to enhance the player's ability, upgrade the player's weapon, etc.
 *  */ 
@ccclass('Weapon')
export class Weapon extends YMoveable {

    @property(SpriteFrame)
    public playerSpriteFrame: SpriteFrame = null;

    @property(CCInteger)
    public bulletSpeed: number = 0;

    @property(CCFloat)
    public bulletInterval: number = 0;
    
    @property(CCInteger)
    public bulletDamage: number = 0;

    @property(CCInteger)
    public numberOfBullets: number = 0;

    @property(SpriteFrame)
    public bulletSpriteFrame: SpriteFrame = null;

    @property(CCInteger)
    public duration: number = 0;

    start() {
        super.start();
    }
    
    onCollisionEnter(self: Collider2D, other: Collider2D){
        //play upgrade sound
        this.node.removeFromParent();
        var sound=this.node.getComponent(AudioSourceComponent);
        if(sound!=null){
            sound.play();
            this.scheduleOnce(()=>{
                this.node.destroy();
            }, 0.5);
        }else{
            this.node.destroy();
        }
    }
    
    update(deltaTime: number): void {
        if(GameContext.instance.gameStatus != GameStatus.Pause){
            super.update(deltaTime);
        }
    }
}


