import { _decorator, Component, Node, UITransform, instantiate, director, Canvas, Sprite, SpriteFrame } from 'cc';
import { GameContext } from './gameContext';
import { GameStatus } from './common/Enums';
const { ccclass, property } = _decorator;

@ccclass('bg')
export class bg extends Component {
    start() {

    }


    adjustLayout() {
        let contentSize = director.getScene().getComponentInChildren(Canvas).getComponent(UITransform).contentSize;
        this.node.getComponent(UITransform).setContentSize(contentSize);
        let bgImg = this.node.children[0];
        bgImg.getComponent(UITransform).setContentSize(contentSize);
    }

    onLoad() {
        this.adjustLayout();
        //canvas size
        let bgImg = this.node.children[0];
        let node = instantiate(bgImg);
        node.scale = bgImg.scale;
        node.parent = this.node;
        node.active = true;
        //this.bg.addChild(node);
        node.setPosition(bgImg.position.x, bgImg.position.y + bgImg.getComponent(UITransform).contentSize.height);
        node.setSiblingIndex(bgImg.getSiblingIndex());
    }

    setBackGroundFrame(spriteFrame: SpriteFrame) {
        if (spriteFrame != null) {
            let bgImg = this.node.children[0];
            bgImg.getComponent(Sprite).spriteFrame = spriteFrame;
            if (this.node.children.length >= 2) {
                let clonedBgImg = this.node.children[1];
                clonedBgImg.getComponent(Sprite).spriteFrame = spriteFrame;

            }
        }
    }
    update(deltaTime: number) {

        if (GameContext.instance.gameStatus != GameStatus.Pause) {

            //console.log(contentSize); 
            //console.log(this.bg.children); 
            //this.bg1.setScale(screenWidth/contentWidth, screenHeight/contentHeight);
            //this.bg2.setScale(screenWidth/contentWidth, screenHeight/contentHeight);
            //this.bg1.getComponent(UITransform).setContentSize(screenWidth, screenHeight);
            //this.bg2.getComponent(UITransform).setContentSize(screenWidth, screenHeight);
            //console.log("screenWidth="+screenWidth+",screenHeight="+screenHeight);
            if (this.node.children.length >= 2) {
                let bgImg = this.node.children[0];
                let contentSize = bgImg.getComponent(UITransform).contentSize;
                let clonedBgImg = this.node.children[1];

                var bg1X = bgImg.position.x;
                var bg1Y = bgImg.position.y;
                var bg2X = clonedBgImg.position.x;
                var bg2Y = clonedBgImg.position.y;
                bg1Y -= 1;
                bg2Y -= 1;
                //IF bg1 is out of screen, then move it to the top of bg2
                if (bg1Y <= -contentSize.height) {
                    bg1Y = bg2Y + contentSize.height;
                }
                //IF bg2 is out of screen, then move it to the top of bg1
                if (bg2Y <= -contentSize.height) {
                    bg2Y = bg1Y + contentSize.height;
                }
                bgImg.setPosition(bg1X, bg1Y);
                clonedBgImg.setPosition(bg2X, bg2Y);
            }
        }
    }
}


