import { Label, tween, CCInteger, resources, Prefab, instantiate, director, Canvas, _decorator, Component, Node, v3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Toast')
export class Toast extends Component {


    public static show(message: string, duration: number = 0, action: Function): Promise<Toast> {
        //dynamic load prefab from resource
        /*****
        let prefab = resources.get<Prefab>("prefabs/ui/dialog");
        let dialog = instantiate(prefab);
        dialog.getComponent(Dialog).title = title;
        dialog.getComponent(Dialog).message = message;
        dialog.getComponent(Dialog).buttons = buttons;
        dialog.setPosition(0, 0);
        dialog.parent = director.getScene().getComponentInChildren(Canvas).node;
        dialog.active = true;
        */
        var promise = new Promise<Toast>((resolve, reject) => {
            resources.load("prefabs/ui/toast", Prefab, (err, prefab) => {

                const dialog = instantiate(prefab);
                dialog.getComponent(Toast).duration = duration;
                dialog.getComponent(Toast).callback = action;
                dialog.getComponent(Toast).message = message;
                dialog.setPosition(0, 0);
                dialog.parent = director.getScene().getComponentInChildren(Canvas).node;
                dialog.active = true;
                resolve(dialog.getComponent(Toast));
            });
        });
        return promise;
    }

    @property(CCInteger)
    public duration: number = 0;

    public message: string = "";

    public callback: Function;

    private messageLines: Array<string> = [];
    private actualMessageLength: number = 0;

    private playAnimation(lineIndex: number) {
        if (lineIndex < this.messageLines.length) {
            let str = this.messageLines[lineIndex];
            this.node.getComponent(Label).string = str;
            //set scale to 0.1
            this.node.setScale(0, 0);
            let duration=this.duration * str.length / this.actualMessageLength;
            tween(this.node).sequence(
                tween().to(duration/3, { scale: v3(1, 1, 1) }, {easing:"elasticOut"}),
                tween().delay(duration/3),
                tween().to(duration/3, { scale: v3(0, 0, 0) }, {easing:"elasticIn"})
            ).call(() => {
                this.playAnimation(lineIndex + 1);
            }).start();
        }
        else {
            if (this.callback != null) {
                this.callback();
            }
            this.node.destroy();
        }
    }
    
    start() {
        //split message into lines
        if (this.duration > 0) {

            let lines = this.message.split("\n");
            lines.forEach(l => {
                if (l != null && l.trim().length>0) {
                    this.actualMessageLength = this.actualMessageLength + l.length;
                    this.messageLines.push(l);
                }
            });

            this.node.getComponent(Label).string = "";
            this.playAnimation(0);
        } else {

            this.node.getComponent(Label).string = this.message;
        }
    }

    update(deltaTime: number) {

    }
}

