import { GameLevel } from "./gameLevel";
import { GameStatus } from "./common/Enums";

export class GameContext {
    //a single tone class to hold the game context
    private static _instance:GameContext=null;
    public static get instance(): GameContext{
        if(GameContext._instance==null){
            GameContext._instance=new GameContext();
        }
        return GameContext._instance;
    }
    private constructor(){}
    //game status


    public presetMessages: Array<string> = [
        "不是我小瞧你，只是我相信你已经尽力了"
        , "一次的失败不代表什么，只要你不放弃，你就还有机会继续失败"
        , "不要气馁，再战再败"
        , "只要肯努力，前面还有很多的失败在等你"
        , "失败并不可怕，可怕的是你还相信这句话"
        , "有时候不努力一下，不知道什么叫绝望"
        , "低处未算低，你还可以做得更差的"
        , "好好玩下去，每次都有新打击"
        , "不要在意别人的眼光，因为别人根本没有在看你"
        , "不要放弃，你下次还可以更糟糕"
        , "先不要失望，因为接下来你会失望很多次"
        , "不要灰心，因为你还有很多次灰心的机会"
        , "这次输了不要紧，因为下次还是会输"
    ];

    // private gameManager: GameManager;

    // constructor(gameManager: GameManager) {
    //     this.gameManager = gameManager;
    // }
    public gameStatus: GameStatus = GameStatus.Start;
    //score
    public score: number = 0;
    //game time
    public gameTime: number = 0;
    //player life
    public life: number = 3;

    private lastEnemyTime: number = 0;

    private lastAwardTime: number = 0;

    /***level settings**** */
    public levelSetting: {
        enemyInterval: number,
        enemyShootThreshold: number,
        enemyShootSpeed: number,
        enemyShootInterval: number,
        enemySpeed: number,
        awardInterval: number,
        levelUpScore: number,
        levelName: string,
        level: number,
    }={
        enemyInterval: 1,
        enemyShootThreshold: 1,
        enemyShootSpeed: 100,
        enemyShootInterval: 1,
        enemySpeed: 300,
        awardInterval: 5,
        levelUpScore: 100,
        levelName: "",
        level: 1,
    };

    public useGameLevel(level: GameLevel) {
        this.levelSetting = {
            enemyInterval: level.enemyInterval,
            enemyShootThreshold: level.enemyShootThreshold,
            enemyShootSpeed: level.enemyShootSpeed,
            enemyShootInterval: level.enemyShootInterval,
            enemySpeed: level.enemySpeed,
            awardInterval: level.awardInterval,
            levelUpScore: level.levelUpScore,
            levelName: level.levelName,
            level: level.level
        };
    }

    //start game and reset all the context
    public reset() {
        this.gameStatus = GameStatus.Start;
        this.score = 0;
        this.gameTime = 0;
        this.life = 3;
        // this.enemyInterval=1;
        // this.enemyShootThreshold=1;
        // this.enemyShootSpeed=100;
        // this.awardInterval=5;
        this.lastEnemyTime = 0;
        this.lastAwardTime = 0;
    }

    public shouldCreateEnemy(deltaTime: number): boolean {
        this.lastEnemyTime += deltaTime;
        if (this.lastEnemyTime >= this.levelSetting.enemyInterval) {
            this.lastEnemyTime = 0;
            return true;
        }
        return false;
    }
    public shouldCreateAward(deltaTime: number): boolean {
        this.lastAwardTime += deltaTime;
        if (this.lastAwardTime >= this.levelSetting.awardInterval) {
            this.lastAwardTime = 0;
            return true;
        }
        return false;
    }
    public randomEnemyShoot(): boolean {
        let rnd=Math.random();
        //console.log(rnd, GameContext.instance.levelSetting.enemyShootThreshold);
        return  rnd>= this.levelSetting.enemyShootThreshold;
    }

}
