import { _decorator, CCInteger, Component } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Explosion')
export class Explosion extends Component {
    @property(CCInteger)
    public removeDelay:number=0.5;
    start() {

    }
    protected onLoad(): void {
        this.scheduleOnce(()=>{
            this.node.destroy();
        }, this.removeDelay);
    }
    update(deltaTime: number) {
        
    }
}

