
import { Canvas } from 'cc';
import { _decorator, Component, Node, Button, Label, EventTouch, director, instantiate, Prefab, resources } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Dialog')
export class Dialog extends Component {

    public static show(title: string, message: string, buttons: Array<{ label: string, action: Function }>, autoDismiss: boolean = true, pauseGame: boolean = false): Promise<Dialog> {
        //dynamic load prefab from resource
        /*****
        let prefab = resources.get<Prefab>("prefabs/ui/dialog");
        let dialog = instantiate(prefab);
        dialog.getComponent(Dialog).title = title;
        dialog.getComponent(Dialog).message = message;
        dialog.getComponent(Dialog).buttons = buttons;
        dialog.setPosition(0, 0);
        dialog.parent = director.getScene().getComponentInChildren(Canvas).node;
        dialog.active = true;
        */
        var promise = new Promise<Dialog>((resolve, reject) => {
            resources.load("prefabs/ui/dialog", Prefab, (err, prefab) => {
                console.log(err);
                const dialog = instantiate(prefab);
                dialog.getComponent(Dialog).title = title;
                dialog.getComponent(Dialog).message = message;
                dialog.getComponent(Dialog).buttons = buttons;
                dialog.getComponent(Dialog).autoDismiss = autoDismiss;
                dialog.getComponent(Dialog).pauseGame = pauseGame;
                dialog.setPosition(0, 0);
                dialog.parent = director.getScene().getComponentInChildren(Canvas).node;
                dialog.active = true;
                resolve(dialog.getComponent(Dialog));
            });
        });
        return promise;
    }

    @property(Label)
    private titleNode: Label = null;
    @property(Label)
    private messageNode: Label = null;
    @property(Button)
    private leftBtn: Button = null;
    @property(Button)
    private rightBtn: Button = null;

    /**
     * auto dismiss after button click
     */
    public autoDismiss: boolean = true;
    public pauseGame: boolean = false;

    public set title(val: string) {
        this.titleNode.string = val;
    }

    public set message(val: string) {
        this.messageNode.string = val;
    }


    start() {
    }

    public dismiss() {
        this.node.destroy();
    }

    /**
     * buttons: [{label:string, action:Function}]
     * label: button label
     * action: button click callback, function(e:EventTouch, dialog:dialog)
     */
    public buttons: Array<{ label: string, action: Function }> = [];

    protected onLoad(): void {
        this.leftBtn.node.active = false;
        this.rightBtn.node.active = false;
        if (this.buttons.length == 1) {
            this.leftBtn.node.active = true;
            this.leftBtn.getComponentInChildren(Label).string = this.buttons[0].label;
            this.leftBtn.node.on(Node.EventType.TOUCH_END, this.onLeftButtonClick, this);
            this.leftBtn.node.setPosition(0, this.leftBtn.node.position.y);
        } else if (this.buttons.length == 2) {
            this.leftBtn.node.active = true;
            this.leftBtn.getComponentInChildren(Label).string = this.buttons[0].label;
            this.leftBtn.node.on(Node.EventType.TOUCH_END, this.onLeftButtonClick, this);
            this.leftBtn.node.setPosition(-75, this.leftBtn.node.position.y);

            this.rightBtn.node.active = true;
            this.rightBtn.getComponentInChildren(Label).string = this.buttons[1].label;
            this.rightBtn.node.on(Node.EventType.TOUCH_END, this.onRightButtonClick, this);
            this.rightBtn.node.setPosition(75, this.leftBtn.node.position.y);
        }
    }

    onLeftButtonClick(e: EventTouch) {
        if (this.buttons[0].action != null)
            this.buttons[0].action(e, this);
        if (this.autoDismiss) {
            this.dismiss();
        }
    }

    onRightButtonClick(e: EventTouch) {
        if (this.buttons[1].action != null)
            this.buttons[1].action(e, this);
        if (this.autoDismiss) {
            this.dismiss();
        }
    }

    update(deltaTime: number) {

    }
}

