import { _decorator, Component, Event, Node, Button, EventHandler, director, EventTouch }  from 'cc';
const { ccclass, property } = _decorator;

@ccclass('button')
export class button extends Component {
    start() {

    }

    update(deltaTime: number) {
        
    }

    onLoad() {
        console.log("button.onLoad");
        //bind onclick event
        this.node.on(Node.EventType.TOUCH_END, this.onBtnClick, this);

    }
    
    onBtnClick(e:EventTouch) {
        director.loadScene("gameplay",function(arg, scene){
            console.log("loadScene callback");
            console.log(arg);
            console.log(scene);
            //director.runScene(scene);
        });
        console.log("click");
        console.log(e);
    }
}


