import { _decorator, clamp, Tween, CCInteger, sys, CCBoolean, AudioClip, AudioSource, tween, Component, Node, EventTouch, Label, Sprite, UITransform, instantiate, director, Prefab, Canvas, Button, v3, PhysicsSystem2D, EPhysics2DDrawFlags } from 'cc';
import { Enemy } from './enemy';
import { YMoveable } from './common/YMoveable';
import { MoveDirection, GameStatus } from './common/Enums';
import { GameContext } from './gameContext';
import { Dialog } from './ui/dialog';
import { Toast } from './ui/toast';
import { GameLevel } from './gameLevel';
import { bg } from './bg';
import { Player } from './player';
const { ccclass, property } = _decorator;

@ccclass('GameManager')
export class GameManager extends Component {
    private static _instance: GameManager = null;
    public static get instance(): GameManager {
        return GameManager._instance;
    }

    @property(CCInteger)
    private beginGameLevelIndex: number = 0;
    @property(Prefab)
    private gameLevels: Array<Prefab> = [];
    private currentLevelIndex: number = 0;
    @property(Prefab)
    private weaponTypes: Array<Prefab> = [];
    @property(Sprite)
    private player: Sprite = null;
    @property(Node)
    private bg: Node = null;
    @property(Prefab)
    private enemyPrefab: Prefab = null;
    @property(CCBoolean)
    private isDebug: boolean = false;
    @property(Button)
    private btnBack: Button = null;
    @property(Node)
    private lblScore: Node = null;
    @property(AudioClip)
    private readyGoSound: AudioClip = null;

    private context: GameContext = null;

    constructor() {
        super();
        GameManager._instance = this;
        this.context = GameContext.instance;
    }

    private beginingAnimation: Tween<Node> = null;
    private beginingAnimationIsRunning: boolean = false;
    adjustLayout() {
        let contentSize = director.getScene().getComponentInChildren(Canvas).getComponent(UITransform).contentSize;
        this.btnBack.node.setPosition(-(contentSize.width * 0.9 - this.btnBack.getComponent(UITransform).contentSize.width) / 2, (contentSize.height * 0.9 - this.btnBack.getComponent(UITransform).contentSize.height) / 2);
        this.lblScore.setPosition(0, (contentSize.height * 0.9 - this.lblScore.getComponent(UITransform).contentSize.height) / 2);


        let playerSize = this.player.node.getComponent(UITransform).contentSize;
        this.player.node.setPosition(0, -contentSize.height / 2 - playerSize.height / 2);
        //set start scale to 2
        this.player.node.setScale(3, 3);
        //target position on screen center middle
        this.beginingAnimationIsRunning = true;
        this.beginingAnimation = tween(this.player.node)
            .to(3, {
                position: v3(0, - playerSize.height / 2, 0)
            }, { easing: 'expoOut' })
            .call(() => {
                this.beginingAnimationIsRunning = false;
            }).start();

        tween(this.player.node)
            .to(3, {
                scale: v3(1, 1, 1)
            }, { easing: 'expoOut' }).start();

        Toast.show("Ready\nGo!", 3, () => {
            //bind onclick event
            this.btnBack.node.on(Node.EventType.TOUCH_END, this.onBackBtnClick, this);
            this.context.gameStatus = GameStatus.Playing;
        });

        this.scheduleOnce(() => {
            // 创建一个空节点
            // 创建 AudioSource 组件
            const audioSource = new Node().addComponent(AudioSource);

            // 设置 AudioSource 的音效资源
            audioSource.clip = this.readyGoSound;
            audioSource.play();
            //audioNode.active=true;
        }, 0.5);
    }

    scoreAdd(score: number = 1) {
        this.context.score = this.context.score + score;
        if (this.context.score >= this.context.levelSetting.levelUpScore && this.context.levelSetting.levelUpScore > 0) {
            this.currentLevelIndex++;
        }
        if (this.currentLevelIndex < this.gameLevels.length) {
            this.changeGameLevel(this.currentLevelIndex);
        }
    }

    changeGameLevel(levelIndex: number) {
        let levelPrefab = this.gameLevels[levelIndex];
        let level = instantiate(levelPrefab).getComponent(GameLevel);
        this.context.useGameLevel(level);
        this.bg.getComponent(bg).setBackGroundFrame(level.gameBgSpriteFrame);
    }

    pauseGame() {

        this.context.gameStatus = GameStatus.Pause;
        Dialog.show("空中斗士", "确认退出游戏？", [
            {
                label: "退出游戏", action: (e: EventTouch, dialog: Dialog) => {
                    this.context.gameStatus = GameStatus.Over;
                    director.loadScene("home");
                }
            },
            {
                label: "继续游戏", action: (e: EventTouch, dialog: Dialog) => {
                    this.context.gameStatus = GameStatus.Playing;
                }
            }
        ]);
    }
    endGame() {
        this.context.gameStatus = GameStatus.Over;
        //TODO: move to gameManager handler
        let newScore = this.context.score;
        let lastScoreNaN = parseInt(sys.localStorage.getItem("lastScore"));
        let maxScoreNaN = parseInt(sys.localStorage.getItem("maxScore"));
        let lastScore = isNaN(lastScoreNaN) ? 0 : lastScoreNaN;
        let maxScore = isNaN(maxScoreNaN) ? 0 : maxScoreNaN;
        maxScore = Math.max(maxScore, newScore);
        sys.localStorage.setItem("lastScore", newScore.toString());
        sys.localStorage.setItem("maxScore", maxScore.toString());
        let randomIndex = Math.floor(Math.random() * this.context.presetMessages.length);
        Dialog.show("挑战失败", this.context.presetMessages[randomIndex]
            + "\n\n今次成绩为：" + newScore
            //+ "\n上次成绩为：" + lastScore
            + "\n最佳成绩为：" + maxScore, [
            {
                label: "返回主页", action: (e: EventTouch, dialog: Dialog) => {
                    director.loadScene("home");
                }
            },
            {
                label: "重新开始", action: (e: EventTouch, dialog: Dialog) => {
                    director.loadScene("gameplay");
                }
            }
        ]);
    }
    start() {
        // this.schedule(() => {
        //     if (this.context.gameStatus == GameStatus.Playing) {
        //         if (this.context.enemyShootThreshold > 0)
        //             this.context.enemyShootThreshold = this.context.enemyShootThreshold - 0.01;

        //         if (this.context.enemyInterval > 0.5)
        //             this.context.enemyInterval = this.context.enemyInterval - 0.01;
        //     }
        // }, 1);
        this.player.getComponent(Player).destoryCallback = (sender: Player) => {
            this.endGame();
        }
        this.currentLevelIndex = this.beginGameLevelIndex;
        this.changeGameLevel(this.currentLevelIndex);
        this.context.reset();
        //enable physicssystem2d
        PhysicsSystem2D.instance.enable = true;
        if (this.isDebug) {
            PhysicsSystem2D.instance.debugDrawFlags = EPhysics2DDrawFlags.All;
        } else {
            PhysicsSystem2D.instance.debugDrawFlags = EPhysics2DDrawFlags.None;
        }
        this.adjustLayout();
        if (sys.isMobile) {
            director.getScene().getComponentInChildren(Canvas).node.on(Node.EventType.TOUCH_MOVE, this.onTouchMoveOfCanvas, this);
        }
        else {
            this.player.node.on(Node.EventType.TOUCH_MOVE, this.onMouseMoveOfPlayer, this);
        }
    }

    //for mouse eviroment, pc, mac, web
    onMouseMoveOfPlayer(e: EventTouch) {
        //if game is not playing, then return
        if (this.context.gameStatus != GameStatus.Playing && this.context.gameStatus != GameStatus.Start)
            return;

        if (this.beginingAnimation != null && this.beginingAnimationIsRunning) {
            //console.log("beginingAnimationIsRunning");
            this.beginingAnimationIsRunning = false;
            this.beginingAnimation.stop();
            //this.player.node.setScale(1, 1);
        }
        //move the player to the touch position
        if (this.player != null && this.player.node != null) {

            // touchmove事件中 event.getLocation() 获取当前已左下角为锚点的触点位置（world point）
            let uiposition = e.getUILocation();
            //console.log("node point: " + uiposition.x + ", " + uiposition.y);
            // 实际hero是background的子元素，所以坐标应该是随自己的父元素进行的，所以要将“world point”转化为“node point”
            let newUILocation =  this.player.node.parent.getComponent(UITransform).convertToNodeSpaceAR(v3(uiposition.x, uiposition.y, 0));
            //console.log("player position: " + this.node.position.x + ", " + this.node.position.y);
            //console.log("new Node location: " + newUILocation.x + ", " + newUILocation.y);
            //if boundary out of screen, then stop move

            let canvasSize = director.getScene().getComponentInChildren(Canvas).node.getComponent(UITransform).contentSize;
            let playerSize = this.player.node.getComponent(UITransform).contentSize;
            newUILocation.x = clamp(newUILocation.x, - canvasSize.width / 2 + playerSize.width / 2, canvasSize.width / 2 - playerSize.width / 2);
            newUILocation.y = clamp(newUILocation.y, - canvasSize.height / 2 + playerSize.height / 2, canvasSize.height / 2 - playerSize.height / 2);

            //set player position
            this.player.node.setPosition(newUILocation);
        }
    }

    onTouchMoveOfCanvas(e: EventTouch) {
        //if game is not playing, then return
        if (this.context.gameStatus != GameStatus.Playing && this.context.gameStatus != GameStatus.Start)
            return;

        if (this.beginingAnimation != null && this.beginingAnimationIsRunning) {
            //console.log("beginingAnimationIsRunning");
            this.beginingAnimationIsRunning = false;
            this.beginingAnimation.stop();
            //this.player.node.setScale(1, 1);
        }
        //move the player to the touch position
        if (this.player != null && this.player.node != null) {
            let playerPos = this.player.node.position;
            let delta = e.getDelta();

            var newX = playerPos.x + delta.x / 2;
            var newY = playerPos.y + delta.y / 2;
            let canvasSize = director.getScene().getComponentInChildren(Canvas).node.getComponent(UITransform).contentSize;
            let playerSize = this.player.node.getComponent(UITransform).contentSize;

            newX = clamp(newX, - canvasSize.width / 2 + playerSize.width / 2, canvasSize.width / 2 - playerSize.width / 2);
            newY = clamp(newY, - canvasSize.height / 2 + playerSize.height / 2, canvasSize.height / 2 - playerSize.height / 2);
            this.player.node.setPosition(newX, newY);

        }
    }
    //back to home scene
    onBackBtnClick(e: EventTouch) {
        //if game is pause, then return
        if (this.context.gameStatus == GameStatus.Start || this.context.gameStatus == GameStatus.Playing) {
            this.pauseGame();
        }
        //director.loadScene("home");
    }

    cloneNode() {
    }

    renderAwards() {
        if (this.weaponTypes.length > 0) {
            //render awards randomly every 5 seconds from this.awardTypes
            let randomIndex = Math.round(Math.random() * (this.weaponTypes.length - 1));
            let awardPrefab = this.weaponTypes[randomIndex];
            let award = instantiate(awardPrefab);
            let awardObj = award.getComponent(YMoveable);
            awardObj.speed = 200;
            awardObj.direction = MoveDirection.MoveDown;
            let canvas = director.getScene().getComponentInChildren(Canvas).node;
            //get screen
            let screen = canvas.getComponent(UITransform).contentSize;
            //set award in random x position, and set y position to screen top, and must bound within award size
            let awardWidth = award.getComponent(UITransform).contentSize.width;
            let awardHeight = award.getComponent(UITransform).contentSize.height;
            //random x between -1/2 screen width to 1/2 screen width
            let awardX = Math.random() * (screen.width - awardWidth) - (screen.width - awardWidth) / 2;
            let awardY = (screen.height - awardHeight) / 2;
            //set award position
            award.setPosition(awardX, awardY);
            //add award to scene
            award.parent = canvas;
            award.active = true;
        }
    }

    //render enemies
    renderEnemies() {
        //get enemy prefab
        //instantiate enemy prefab
        let enemy = instantiate(this.enemyPrefab);
        let enemyObj = enemy.getComponent(Enemy);
        //random enemy speed [200, 500]
        enemyObj.speed = Math.random() * this.context.levelSetting.enemySpeed + this.context.levelSetting.enemySpeed;

        enemyObj.direction = MoveDirection.MoveDown;
        enemyObj.destoryCallback = (sender: Enemy) => {
            this.scoreAdd();
        }

        enemyObj.bulletInterval = this.context.levelSetting.enemyShootInterval;
        //set bullet damage
        //enemyObj.bulletDamage = this.context.levelSetting.enemyShootDamage;
        enemyObj.bulletSpeed = Math.random() * this.context.levelSetting.enemyShootSpeed + 50;
        enemyObj.target = this.player;
        let canvas = director.getScene().getComponentInChildren(Canvas).node;
        //get screen
        let screen = canvas.getComponent(UITransform).contentSize;
        //set enemy in random x position, and set y position to screen top, and must bound within enemy size
        let enemyWidth = enemy.getComponent(UITransform).contentSize.width;
        let enemyHeight = enemy.getComponent(UITransform).contentSize.height;
        //random x between -1/2 screen width to 1/2 screen width
        let enemyX = Math.random() * (screen.width - enemyWidth) - (screen.width - enemyWidth) / 2;
        let enemyY = (screen.height - enemyHeight) / 2;
        //set enemy position
        enemy.setPosition(enemyX, enemyY);
        //add enemy to scene
        enemy.parent = canvas;
        enemy.active = true;
    }

    update(deltaTime: number) {
        const score = this.context.score;
        const levelUpScore = this.context.levelSetting.levelUpScore;
        const level = this.context.levelSetting.level;
        const levelName = this.context.levelSetting.levelName;
        this.lblScore.getComponent(Label).string = "关卡 " + level + " 分数 " + score + (levelUpScore > 0 ? " 目标 " + levelUpScore : "");

        //if game is pause, then return
        if (this.context.gameStatus == GameStatus.Playing) {
            //render enemies every 1 second
            if (this.context.shouldCreateEnemy(deltaTime)) {
                this.renderEnemies();
            }
            //render awards every 5 seconds
            if (this.context.shouldCreateAward(deltaTime)) {
                this.renderAwards();
            }
        }
    }
}

