import { _decorator, Component, Vec3, CCInteger, Node, director, Canvas, instantiate, AudioClip, AudioSourceComponent, ParticleSystem2D, ParticleSystemComponent, UITransform, Collider2D, Contact2DType, IPhysics2DContact, PhysicsSystem2D, Prefab } from 'cc';
import { YMoveable } from "./common/YMoveable";
import { Bullet } from "./bullet";
import { Explosion } from "./common/explosion";
import { BulletTargetType, MoveDirection, GameStatus } from './common/Enums';
import { GameContext } from './gameContext';
const { ccclass, property } = _decorator;

@ccclass('Enemy')
export class Enemy extends YMoveable {

    public bulletInterval: number = 1;
    @property(CCInteger)
    public bulletSpeed: number = 50;
    @property(CCInteger)
    public bulletDamage: number = 10;
    private lastShootTime: number = 0;

    @property(Prefab)
    private explosionPrefab: Prefab = null;
    @property(Prefab)
    private bulletPrefab: Prefab = null;

    public target: Component;

    constructor() {
        super();
        this.direction = MoveDirection.MoveDown;
    }
    start() {
        super.start();
        this.lastShootTime = this.bulletInterval;
    }

    update(deltaTime: number): void {
        if (GameContext.instance.gameStatus != GameStatus.Pause) {
            super.update(deltaTime);
            this.lastShootTime += deltaTime;
            if (this.lastShootTime >= this.bulletInterval) {
                if (GameContext.instance.randomEnemyShoot()) {
                    this.shoot();
                }
                this.lastShootTime = 0;
            }
        }
    }
    //碰撞检测
    onCollisionEnter(self: Collider2D, other: Collider2D) {
        //play fire sound
        if (this.node.active) {
            this.node.active = false;
            if (this.explosionPrefab) {
                let explosionNode = instantiate(this.explosionPrefab);
                explosionNode.setPosition(this.node.position);
                explosionNode.parent = this.node.parent;
                explosionNode.getComponent(UITransform).setContentSize(this.node.getComponent(UITransform).contentSize);
                explosionNode.getComponent(Explosion).removeDelay = 0.2;
                explosionNode.active = true;
            }
            if (this.destoryCallback != null)
                this.destoryCallback(this);
            this.node.destroy();
        }
    }

    //shoot bullet to attack player
    shoot() {
        //create bullet
        if (this.target != null && this.target.node != null) {
            let bulletNode = instantiate(this.bulletPrefab);
            bulletNode.parent = this.node.parent;
            bulletNode.setPosition(this.node.position);
            var bulletObj = bulletNode.getComponent(Bullet);
            bulletObj.sender = this;
            bulletObj.damage = this.bulletDamage;
            bulletObj.speed = this.bulletSpeed;
            bulletObj.direction = MoveDirection.MoveXY;
            bulletObj.targetType = BulletTargetType.ToPlayer;
            bulletObj.targetXY = director.getScene().getComponentInChildren(Canvas).getComponent(UITransform).convertToWorldSpaceAR(this.target.node.position);
        }
    }
}


